import React from 'react';
import './Support_sect.css';

function Support_sect (){
    return(
        <div className="support-section">
            <div className="support-container">
            <h2>Frequently Asked Questions
            </h2>
            <p>Q: Is it necessary to finish every homework pertaining to each module? <br></br>
            A: It is advised to finish each homework as it would benefit your understanding 
            in each module and your own emotions

            <br/><br/>
            Q: I see there is a chat system, do I have to chat with other people in any of the sessions?<br></br>
            A: you do not need to chat with other participants but if you do intend to use the function, please DO NOT share
            personal information or converse in illicit topics
            <br/><br/>
            Q: would I be able to use the App offline?
            <br/>
            A: Yes, however some features and educational materials would be missing such as videos within modules/group chats/homework
            <br/><br/>
            Q: Do I have to participate in module discussion (text)?
            <br/>
            A: No, you do not need to participate in group discussions, however it would help broaden your understanding
            of the corresponding topic.
            <br></br><br></br>
            Q: I see homeworks are done in a survey format, is there a way for me to review the homework I've done?
            <br></br>
            A: Yes, for each homework you'd prompted on giving out your email. After you finish your homework, Qualtrics would send an
            email you with the attached answers of your homework.
            <br></br><br></br>
            Q: Do I have to finish the entire homework corresponding to the module?
            <br></br>
            A: Your homework gets saved and you can always go back and finish it.
            <br></br><br></br>
            Q: Is there a deadline to finishing homework/Modules?
            <br></br>
            A: You will be briefed on the deadlines by your coordinator during sessions.
            </p>    

            </div>
        </div>
    );
}

export default Support_sect;
